@import url("https://fonts.googleapis.com/css2?family=Noto+Serif+KR:wght@500&display=swap");

* {
  font-family: "Noto Serif KR", serif;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 5vh;
}

section h1 {
  text-align: center;
  margin: 5vh;
}

.invitation {
  max-width: 75%;
}
.wedding-hall {
  max-width: 75%;
  word-break: keep-all;
}
.wedding-hall-detail {
  font-size: 13px;
  word-break: keep-all;
}
.Layout {
  padding: 0 0%;
  display: flex;
  flex-direction: column;
}

@media (min-width: 1024px) {
  .Layout {
    padding: 0 25%;
    display: flex;
    flex-direction: column;
  }
  .invitation {
    max-width: 50%;
  }
  .wedding-hall {
    max-width: 50%;
  }
  .maeum-sentence {
    max-width: 50%;
  }
}
.map-icon {
  max-width: 50px;
  margin: 0 10%;
}
.intro-container {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  text-align: center;
}
.intro-image {
  height: 100vh;
  margin: 0 -100%;
}
.intro-wish {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgba(64, 64, 64);
  font-size: 50px;
  word-break: keep-all;
}
.intro-names {
  position: absolute;
  left: 60%;
  top: 27.5%;
  color: black;
  font-size: 30px;
}
.intro-hall {
  font-size: 20px;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-align: center;
}
.intro-hall p {
  word-break: keep-all;
}

.map-groups {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 3vh;
}

.map-image {
  max-width: 80%;
}

.maeum-sentence {
  max-width: 75%;
}

.comment-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.comment-name {
  font-weight: bold;
  color: black;
}
.comment-date {
  color: gray;
  font-size: 12px;
  text-align: right;
}
.comment-text {
  font-style: italic;
  text-align: center;
}
.comment-create-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
